/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
.vdl-hint {
  color: #414142; }
  .vdl-hint.vdl-error {
    color: #b1181e; }

.vdl-hint ~ .vdl-hint::before {
  content: '-';
  margin: 0 5px; }
